.footer{

  .footer__block-menu{
    border-top: 1px solid rgba(31, 32, 65, 0.1);
    border-bottom: 1px solid rgba(31, 32, 65, 0.1);
    display: flex;
    justify-content: center;
    z-index: -2;
    width: 100%;
    position: relative;
    background: #fff;

    .footer__content-container{
      flex-grow: 1;
      max-width: 80.5%;
      min-width: 400px;
      margin: 0 auto;
      background: #fff;

      .footer__bottom-menu{
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        margin: 0;
        padding: 91px 0 89px 0;
        font-size: 14px;
        color: rgba(31, 32, 65, 0.5);
      
        .footer__bottom-menu-logo-block{

          .footer__logo{
            margin: 10px 0 3px;
          }
        }

        .footer__menu-column,
        .footer__bottom-menu-logo-block{
          display: block;
          max-width: min-content;
      
          .footer__menu-list{
            padding: 0;
      
            .footer__menu-li{
              position: relative;
              font-size: 14px;
              line-height: 17px;
              padding: 7px 0;
              line-height: 24px;
              cursor: pointer;              
              display: block;
              color: rgba(31, 32, 65, 0.5);
              
              &.menu_li__active{
                font-weight: bold;
                color: rgba(31, 32, 65, 0.75);
              }
      
              &:first-child{
                font-weight: bold;
                color: #1F2041;
                font-size: 12px;
                text-transform: uppercase;
                cursor: inherit;
                white-space: nowrap;
                padding: 10px 0 7px;
              }
            }
      
            .input-text_subscription{
              margin-top: 10px;
      
              .input-text__input{
                padding: 10px 14px;
                font-size: 14px;
                width: 260px;
              }
            }
          }
      
          &:last-child,&:first-child{
            min-width: 200px;
            color: rgba(31, 32, 65, 0.75);
      
            .footer__menu-li{
              cursor: inherit;
              color: rgba(31, 32, 65, 0.75);
            }
          }
      
          &:nth-child(3){
            min-width: 155px;
          }
      
          &:nth-child(2),&:nth-child(4){
            min-width: 155px;
          }
        }
      
        .footer__bottom-menu-logo-block{
          max-width: 255px;
      
          p{
            padding: 0;
          }
        }
      }
    }
  }

  .footer__copyright-block{
    background: #fff;
    box-shadow: 0px 10px 20px rgba(31, 32, 65, 0.05);

    .footer__copyright-content{
      display: flex;
      justify-content: space-between;
      align-items: center;
      font-size: 14px;
      color: rgba(31, 32, 65, 0.5);
      min-width: 400px;
      max-width: 80.5%;
      margin: 0 auto;

      p{
        margin: 23px 0;
      }
    
      .footer__soc-block{
        display: flex;
      
        .footer__soc-network{
          height: 24px;
          width: 24px;
          padding-left: 20px;
    
          &.footer__soc-network_twitter{
            margin-top: -1px;
          }
    
          &.footer__soc-network_facebook{
            width: 22px;
            height: 22px;
            margin-top: 1px;
          }
    
          &.footer__soc-network_instagram{
            width: 22px;
            height: 22px;
            margin-top: 1px;
          }
        }
      }
    }
  }
}

@media (max-width: 1170px){
  .footer{

    .footer__bottom-menu{
      text-align: center;
      justify-content: space-around;

      .footer__menu-column{
        margin: 10px 20px;

        .footer__menu-list{
          width: 270px;
        }
      }

      .footer__logo{
        margin: 10px 20px;
        max-width: none;
      }
    }

    .footer__copyright-content{
      margin: 23px 20px;
    }
  }
}

@media (max-width: 790px){
  .footer{

    .footer__bottom-menu{
      display: flex;
      flex-direction: column;
      align-items: center;
    }
  }
}

@media (max-width: 620px){
  .footer{

    .footer__bottom-menu{
      padding: 90px 0 0 0;
      
      .footer__menu-column{
        display: none;
      }

      .footer__logo{
        margin: 0;

        p{
          display: none;
        }
      }
    }
  }
}